@import "https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,300;0,400;0,500;0,600;0,800;1,800&display=swap";
body {
  background: #0a0224;
  margin: 0;
  font-family: Work Sans, sans-serif;
}

a {
  color: #f5f5f5;
  text-decoration: none;
}

header {
  background-image: linear-gradient(#2499f844, #5c208dab, #208d8dab, #292cdcc1), url("Avatar.67adb506.jpg");
  background-position: 50% 21%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 100px 80px;
  position: relative;
}

.hide {
  color: #f5f5f5;
  margin-top: 5px;
  margin-right: 10px;
  font-size: 20px;
}

.main-search-field {
  max-width: 600px;
  width: 600px;
  margin: 0 auto 30px;
  display: flex;
  position: absolute;
  top: 280px;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (max-width: 450px) {
  .main-search-field {
    width: 90%;
    flex-wrap: wrap;
    gap: .5em;
    margin: 0 auto;
  }

  .main-search-field input {
    width: 80%;
    border-radius: 6px;
    flex-grow: 1;
  }

  .main-search-field button {
    width: 20%;
    border-radius: 6px;
    flex-grow: 1;
  }
}

.main-search-field input {
  color: #f5f5f5;
  background: none;
  border: 1px solid #ebedef;
  border-radius: 10px 0 0 10px;
  padding: 10px 40px;
  font-size: 1em;
}

.main-search-field input::placeholder {
  color: #becefa;
}

.main-search-field button {
  color: #f5f5f5;
  cursor: pointer;
  text-shadow: 2px 2px 3px #2424248e;
  background: #076eeb;
  border: 1px solid #ebedef;
  border-radius: 0 10px 10px 0;
  padding: 10px 20px;
  font-size: 20px;
  transition: all .5s;
  display: flex;
}

.main-search-field button:hover {
  color: #f5f5f5;
  border-radius: 0 25px 25px 0;
}

.box-search {
  display: flex;
  box-shadow: 3px 3px 4px #1d1d1d56;
}

.box-1 {
  justify-content: space-between;
  font-size: 80px;
  display: flex;
}

.logo {
  color: #f5f5f5;
  margin-left: 60px;
  font-size: 40px;
  font-weight: bold;
}

@media screen and (max-width: 450px) {
  .logo {
    margin-left: 20px;
  }
}

nav {
  z-index: 99;
  width: 100%;
  background: linear-gradient(#2499f844, #5c208dab);
  position: fixed;
}

nav .wrapper {
  max-width: 1300px;
  height: 70px;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 30px;
  line-height: 70px;
  display: flex;
  position: relative;
}

.wrapper .logo a {
  color: #f2f2f2;
  font-size: 30px;
  font-weight: 600;
  text-decoration: none;
}

.wrapper .nav-links {
  display: inline-flex;
}

.nav-links li {
  list-style: none;
}

.nav-links li a {
  color: #f2f2f2;
  border-radius: 5px;
  align-content: center;
  align-items: center;
  padding: 9px 15px;
  font-size: 20px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}

.nav-links li a:hover {
  color: #834efd;
  background: #3a3b3c;
  border-bottom: 2px solid #834efd;
}

.wrapper .btn {
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  display: none;
}

.wrapper .btn.close-btn {
  position: absolute;
  top: 10px;
  right: 30px;
}

@media screen and (max-width: 970px) {
  .wrapper .btn {
    display: block;
  }

  .wrapper .nav-links {
    height: 100vh;
    width: 100%;
    max-width: 350px;
    background: #010530;
    padding: 50px 10px;
    line-height: 50px;
    transition: all .3s;
    display: block;
    position: fixed;
    top: 0;
    left: -100%;
    overflow-y: auto;
    box-shadow: 0 15px 50px #834efd;
  }

  ::-webkit-scrollbar {
    width: 10px;
  }

  ::-webkit-scrollbar-track {
    background: #242526;
  }

  ::-webkit-scrollbar-thumb {
    background: #3a3b3c;
  }

  #menu-btn:checked ~ .nav-links {
    left: 0%;
  }

  #menu-btn:checked ~ .btn.menu-btn {
    display: none;
  }

  #close-btn:checked ~ .btn.menu-btn {
    display: block;
  }

  .nav-links li {
    margin: 15px 10px;
  }

  .nav-links li a {
    padding: 0 20px;
    font-size: 20px;
    display: block;
  }
}

nav input {
  display: none;
}

.nav-links {
  line-height: 60px;
  list-style: none;
  display: flex;
}

.nav-items {
  height: 60px;
  cursor: pointer;
  text-transform: uppercase;
  width: max-content;
  border-radius: 10px;
  margin: 0 10px;
  padding: 0 15px;
  font-size: 18px;
  transition: all .4s;
}

.log {
  width: 150px;
  padding: 5px;
}

